import { Link } from "gatsby"
import React from "react"
import styled from 'styled-components'
import GlossySelfie from '../images/glossyselfie.jpg'


const Header = (props) => {

  const Menubar = styled.div`
  height: 100%; 
  width: 20em; 
  position: fixed; 
  z-index: 1; 
  top: 0; 
  left: 0;
  background-color: beige;
  overflow-x: hidden;
  padding-top: 20px;
  display: flex;
  flex-direction: column;

  @media (max-width: 820px) {
    height: 12em;
    width: 100%;
    flex-direction: row;
  }
  `
  const ContentContainer = styled.div`
  display: flex;
  flex-direction: column;
  margin: 1em;
  @media (max-width: 820px) {
    flex-direction: row;
  }
  `

  const Bio = styled.div`
  margin: 1em;
  `;

  const Pic = styled.img.attrs({
    src: GlossySelfie,
  })`
  width: 200px;
  height: 300px;
  border: 1px solid blue;
  `
  console.log(GlossySelfie)
  return (
    <div>
      <Menubar>
        <ContentContainer>
          {/* 
        <Link to="/about"> /about  </Link>
        <Link to="/projects"> /projects </Link>
        <Link to="/"> / </Link> 
        */}

          <Pic />

          <Bio>I am a software engineer based in Seattle, passionate about improving the human experience, and solving challanges that impact peoples lives. I'm a sucker for a fresh perspective on life, and a day at the beach in a city I've never seen.</Bio>
        </ContentContainer>
      </Menubar>
    </div>
  )
}

export default Header
